import React from "react"
import Layout from "../components/layout"
import "../components/application.css"

const application = () => {
  return (
    <Layout>
      <div
        style={{
          display: `flex`,
          justifyContent: `center`,
          alignItems: `center`,
          flexDirection: `column`,
        }}
      >
        <h1 style={{ margin: 0, padding: `2em 0` }}>Course Application</h1>
        <div className="application-container">
          <form action="https://formspree.io/mzbjbnaj" method="POST" id="form">
            <label htmlFor="name">Name</label>
            <input
              placeholder="John Doe"
              type="text"
              name="ApplicantName"
              id="applicantName"
              required
            />
            <label htmlFor="phone">Phone number</label>
            <input
              placeholder="+91 12345 54321"
              type="text"
              name="phone"
              id="phone"
              required
            />
            <label htmlFor="course">Select Course</label>
            <select
              id="courseSselect"
              name="course-select"
              form="courseForm"
              required
            >
              <option value="Javascript">Javascript</option>
              <option value="HTML and CSS">HTML and CSS</option>
              <option value="Python">Python</option>
            </select>
            <input type="submit" value="Submit" />
          </form>
        </div>
      </div>
    </Layout>
  )
}

export default application
